    border-radius: 20px;
}

.Aboutparent {
    background-image: linear-gradient( 128deg, #FFFFFF 36%, rgb(0 0 0 / 35%) 36%, rgb(206 206 206 / 41%) 62%, #FFFFFF 60%), url(/smym-cake-order/static/media/Aboutbg.0127d357.jpg);
    height: 80vh;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

h1 {
    text-align: center;
}

.maincontent {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin: 21vh 4vw 0vh 4vw;
}

.contact p {
    color: black;
}
.contact p a{
