}

.presentation p {
    font-size: 2rem;
    /* background-color: linear-gradient( 100deg, #FFFFFF 36%, rgba(255, 255, 255, 0) 36%, rgba(255, 255, 255, 0) 62%, #FFFFFF 60%); */
    background: -webkit-linear-gradient(310deg, #001AFF 27.95%, #FF7A00 89.63%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.presentation p:nth-child(2) {
    font-size: 1.5rem;
    background: -webkit-linear-gradient(92.58deg, #DB00FF 27.95%, #FF3D00 89.63%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@media only screen and (max-width: 900px) {
