/* Alert Box CSS */

/* .alertDiv{
    height: 100vh;
    position: absolute;
    z-index: 999;
} */

.alert-container {
  width: 100%;
  position: fixed;
  bottom: 40px;
  display: none;
  justify-content: center;
  z-index: 999;
  transition: 2s ease-in;
}
.alert-container-show {
  display: flex;
}

.alert-box {
  height: 60px;
  background-color: #228c22;
  color: white;
  padding: 0.5rem;
  border-radius: 5px;
  width: calc(300px + 10vw);
  border-left: 5px solid #003700;
}
.alert-box.show {
  -webkit-animation: show_pop 1s ease forwards;
          animation: show_pop 1s ease forwards;
}
@-webkit-keyframes show_pop {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  40% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  80% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  100% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
}
@keyframes show_pop {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  40% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  80% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  100% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
}
.alert-box.hide {
  -webkit-animation: hide_pop 1s ease forwards;
          animation: hide_pop 1s ease forwards;
}
@-webkit-keyframes hide_pop {
  0% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  40% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  80% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  100% {
    -webkit-transform: translateY(155%);
            transform: translateY(155%);
  }
}
@keyframes hide_pop {
  0% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  40% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
  80% {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
  }
  100% {
    -webkit-transform: translateY(155%);
            transform: translateY(155%);
  }
}
