.printBtnDiv{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 18rem;
}
.printBtn {
    padding: .4rem 1.5rem;
    margin: 2rem 0rem;
    background-color: blue;
    font-size: 1.2rem;
    font-weight: 600;
    color: white;
    text-align: center;
    border: 1px solid white;
    border-radius: 5px;
    transition: .8s ease;
}

.printBtn:hover {
    background-color: rgb(0, 0, 73);
    color: white;
}

@media print {
    .dataCollection {
        margin: 3rem;
